.Burger{
    width:100%;
    height:250px;
    margin:auto;
    overflow: auto;
    font-size:1.2rem;
    font-weight:bold;
    text-align:center;
}

@media (min-width:500px) and (min-height:400px){
    .Burger{
        width:350px;
        height:300px;
        margin-top:20px;
    }
}
@media (min-width:500px) and (min-height:401px){
    .Burger{
        width:450px;
        height:400px;
        margin-top:30px;
    }
}
@media (min-width:1000px) and (min-height:700px){
    .Burger{
        width:700px;
        height:600px;
        margin-top:55px;
    }
}